body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper{
    position: fixed;
    top: 1rem;
    right: 0.5rem;
    display: inline-block;
    padding: 10px 20px;
    max-width: 20rem;
    color: white;
}

.success{
    background: green;
}

.error{
    background: red;
}
.container-fluid{
    background-color: #607D8B;
}

.full{
    height: 100vh;
}

.box{
    margin: auto;
}

h2{
    text-align: center;
}

.card{
    background-color: #455A64;
}

.input{
    width: 100%;
    background: transparent;
    padding: 10px;
    border-color: #CFD8DC;
    border-radius: 50px;
    color: white;
    margin-top: 1rem;
}

input::-webkit-input-placeholder{
    color: whitesmoke;
}

input::-moz-placeholder{
    color: whitesmoke;
}

input::-ms-input-placeholder{
    color: whitesmoke;
}

input::placeholder{
    color: whitesmoke;
}

.rememberme{
    display: block;
    margin-top: 1rem;
}

.rememberme label{
    padding-left: 1rem;
}

.btn-holder{
    display: block;
    text-align: center;
    margin-top: 1rem;
}

.btn-holder button{
    border-radius: 50px;
    font-size: 1.5rem;
    width: 50px;
    height: 50px;
    background-color: #757575;
    outline: none;
}
